@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400&display=swap);
.ItemList {
    margin: 1rem 0;
    list-style-type: none;
    padding: 0;
    overflow: scroll;
}
  
.Item {
    font-size: 1.2rem;
    padding: 1rem 0.5rem;
    border-bottom: 1px solid #eceff1;
}

.Item span {
    cursor: pointer;
}

.remove-btn {
    margin-left: 0.75rem;
    padding: 0.3rem;
    border-radius: 0.3rem;
    color: #00897b;
    cursor: pointer;
}

.remove-btn:hover {
    color: red;
}
h3 {
    margin: 0;
    margin-bottom: 1rem;
    font-weight: bold;
    font-size: 3rem;
    position: relative;
    color: #00897b;
    text-align: left;
}

.todo-input {
    width: 100%;
    font-size: 1.2rem;
    padding: 0.5rem 0.5rem;
    box-sizing: border-box;
    border: none;
    border-bottom: 1px solid #90a4ae;
}
  
.todo-input:focus {
    border-bottom: 1px solid #00897b;
    outline: 0;
}
  
.todo-input:focus::-webkit-input-placeholder {
    color: #00897b;
}
  
.todo-input:focus:-ms-input-placeholder {
    color: #00897b;
}
  
.todo-input:focus::-ms-input-placeholder {
    color: #00897b;
}
  
.todo-input:focus::placeholder {
    color: #00897b;
}

.todo-input::-webkit-input-placeholder {
    color: #90a4ae;
    font-weight: 100;
}

.todo-input:-ms-input-placeholder {
    color: #90a4ae;
    font-weight: 100;
}

.todo-input::-ms-input-placeholder {
    color: #90a4ae;
    font-weight: 100;
}

.todo-input::placeholder {
    color: #90a4ae;
    font-weight: 100;
}

.add-todo {
    margin-top: 0.75rem;
    border: 1px solid #00897b;
    border-radius: 0.2rem;
    cursor: pointer;
}

.add-todo:hover {
    background-color:orange;
}
  
.add-todo:focus {
    background-color: #00897b;
}
h3 {
    margin: 0;
    margin-bottom: 1rem;
    margin-top: 1rem;
    font-weight: bold;
    font-size: 1.5rem;
    position: relative;
    color: #00897b;
    text-align: left;
}

