}

.add-goal {
    margin-top: 0.75rem;
    border: 1px solid #00897b;
    border-radius: 0.2rem;
    cursor: pointer;
}

.add-goal:hover {
    background-color:orange;
}
  
.add-goal:focus {
    background-color: #00897b;
}
.App {
    width: auto;
    max-width: auto;
    height: auto;
    max-height: auto;
    margin: 1rem;
    background: #fff;
    padding: 1.5rem;
    border-radius: 8px;
    box-shadow: 0px 1px 2px 0px rgba(50, 50, 50, 0.2);
    color: #263238;
    display: flex;
    flex-direction: column;
}

.App h1 {
    margin: 0;
    margin-bottom: 1.5rem;
    font-weight: 200;
    font-size: 3rem;
    position: relative;
    color: #00897b;
    text-align: center;
}

.App h5 {
    margin: 0;
    margin-bottom: 1rem;
    font-style: italic;
    font-size: 0.75rem;
    position: relative;
    color: #00897b;
    text-align: center;
}
body {
  margin: 0;
  background: #f6f9fd;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  font-family: "Open Sans", sans-serif;
}

#root {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
